import React, { Fragment, useState, useEffect } from 'react';
import Menu from '../layouts/Menu';
import Mobilemenu from '../layouts/Mobilemenu';
import HeaderComponent from '../../helper/Navigationhelper';
import { Link } from 'react-router-dom';
import classNames from 'classnames';
import Cookies from 'js-cookie';
import { loginuser,requestListing } from '../../api';
import { useHistory } from 'react-router';


const Header = () => {
    const stickyheader = 'sticky';
    const [user, setuser] = useState({});
    const [navtoggle, setnavtoggle] = useState(false);
    // console.log("user.limit");
    // console.log(user.limit);
   
    let history = useHistory();

    useEffect(() => {
        loginuser({
            token: Cookies.get('token')
        })
            .then(function (response) {
                // console.log("header rewsponse", response);
                if (response.data.message == "true") {
                    try {
                        setuser(response.data.user);
                        // console.log("limit=", response.data.user.limit)
                    } catch (e) {
                        return null;
                    }
                } else if (response.data.message === "false") {

                }

            })
            .catch(function (error) {

            });

    }, [user]);

    const navtoggleClass = () =>{
        setnavtoggle(!navtoggle)
    }

    const Submit = ()=>{

        history.push('/submit-listing');

    }

    const onRequest = ()=>{
        
        requestListing({
            token: Cookies.get('token')
            
        })
        .then(function (response) {
            
            user=null;
            console.log(response.data.message);
            
        })
        
        .catch(function (error) {

        });
        
        

    }

    return (
        <Fragment>
            {/* Aside (Mobile Navigation) */}
            <aside className={classNames("main-aside", { "open": navtoggle })}>
                <div className="aside-title">
                    <div className="aside-controls aside-trigger">
                        <h4>Menu</h4>
                        <div className="close-btn close-dark" onClick={()=>navtoggleClass()} >
                            <span />
                            <span />
                        </div>
                    </div>
                </div>
                <Mobilemenu />
            </aside>
            <div className="aside-overlay aside-trigger" onClick={()=>navtoggleClass()}/>
            {/* Header Start */}
            <header className={`main-header header-fw can-sticky header-1 ${stickyheader}`}>
                {/* Top Header Start */}
                <div className="top-header">
                    <div className="top-header-inner">
                        <ul className="social-media">
                            <li> <Link to="#"> <i className="fab fa-facebook-f" /> </Link> </li>
                            <li> <Link to="#"> <i className="fab fa-pinterest-p" /> </Link> </li>
                            <li> <Link to="#"> <i className="fab fa-linkedin-in" /> </Link> </li>
                            <li> <Link to="#"> <i className="fab fa-twitter" /> </Link> </li>
                        </ul>
                        <ul className="top-header-nav">
                            {Cookies.get('mail') === undefined ? <><li> <Link to="/login"> Login</Link> </li>
                                <li>or</li>
                                <li> <Link to="/register"> Signup</Link> </li></> : <Link className="logout" to="/" onClick={() => {
                                    Cookies.remove('token');
                                    Cookies.remove('mail');
                                }}><i className="flaticon-shut-down-button" /> Logout</Link>}
                            {/* <li> <Link to="/login"> Login</Link> </li>
                                <li>or</li>
                                <li> <Link to="/register"> Signup</Link> </li> */}
                        </ul>
                    </div>
                </div>
                {/* Top Header End */}
                <nav className="navbar">
                    {/* Menu */}
                    <Menu />
                    <div className="header-controls">
                        <ul className="header-controls-inner d-none d-lg-flex">
                            {Cookies.get('type') === "company" ? (
                                ""
                            ) : (
                                <li>
                                    {Cookies.get('mail') === undefined ? <Link to="/login" className="btn-custom primary" onClick={()=>Submit()}>Submit Listing <i className="fas fa-plus" /> </Link> 
                                    :
                                        user.limit > 0 ?
                                            (
                                                <Link to="/submit-listing" className="btn-custom secondary mr-0" onClick={()=>Submit()} >Submit Listing <i className="fas mr-0 fa-plus" /> </Link>

                                            ) : user.access === "requested" ? (
                                                <button className="btn-custom secondary mr-0">Request sent <i className="fas mr-0 fa-plus" /> </button>

                                            ):(
                                                <button className="btn-custom secondary mr-0" onClick={()=>onRequest()}>Request Listings <i className="fas mr-0 fa-plus" /> </button>
                                            )}
                                            {/* (<button className="btn-custom secondary mr-0" onClick={()=>onRequest()}>Request Listings <i className="fas mr-0 fa-plus" /> </button>)} */}
                                    {/* <Link to="/submit-listing" className="btn-custom primary">Submit Listing <i className="fas fa-plus" /> </Link> */}
                                </li>
                            )}

                        </ul>
                        {/* Toggler */}
                        <div className="aside-toggler aside-trigger" onClick={()=>navtoggleClass()}>
                            <span />
                            <span />
                            <span />
                        </div>
                    </div>
                </nav>
            </header>
            {/* Header End */}
        </Fragment>
    );
}

export default Header;